:root {
  --primary: #3A1C71;
  --primary-light: #5E4FA2;
  --primary-dark: #290E50;
  --secondary: #FF5F6D;
  --secondary-light: #FF8E9A;
  --secondary-dark: #D03440;
  --accent: #FFC371;
  --accent-light: #FFD399;
  --accent-dark: #D99C45;
  --background: #ffffff;
  --foreground: #111827;
  --gradient-primary: linear-gradient(45deg, #3A1C71, #D76D77, #FFAF7B);
  --gradient-secondary: linear-gradient(135deg, #FF5F6D, #FFC371);
  --gradient-accent: linear-gradient(90deg, #5E4FA2, #FF5F6D);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0F0F14;
    --foreground: #F9FAFB;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideUpFade {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
@keyframes gradientFlow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.animate-fade-in {
  animation: fadeIn 0.6s ease-in-out;
}
.animate-slide-up {
  animation: slideUpFade 0.8s ease-out;
}
.animate-pulse-slow {
  animation: pulse 3s infinite ease-in-out;
}
.gradient-text {
  background: var(--gradient-primary);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 200%;
  animation: gradientFlow 3s ease infinite;
}
.gradient-border {
  position: relative;
  padding: 2px;
}
.gradient-border::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  padding: 2px;
  background: var(--gradient-secondary);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, 
    linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, 
    linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans), ui-sans-serif, system-ui, sans-serif;
  scroll-behavior: smooth;
}
.text-balance {
  text-wrap: balance;
}
.section-padding {
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .section-padding {
    padding: 7.5rem 4rem;
  }
}
.glass-effect {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
